
* {
  padding: 0;
  margin: 0;
}

header {
  background-color: rgb(1, 71, 72);
  padding: 20px;
  display: flex;
  flex-direction: column;
  position: sticky;
  top: 0;
  z-index: 10;
}

header input {
  outline: none;
  padding: 10px;
  height: 50px;
  margin-left: 25px;
  margin-top: 25px;
  border-radius: 15px;
  flex: 1;
}

header img {
  width: 100px;
  height: auto;
  border-radius: 50px;
}

.bi-person-circle {
  font-size: 1rem;
  color: rgb(225, 244, 21);
}

header a {
  text-decoration: none;
  font-size: large;
}

#login {
  display: flex;
  padding: 30px;
  flex-wrap: wrap;
}
.spanhero {
  color: blue;
  font-size: large;
}


.navigation {
  background-color: rgb(186, 238, 207);
  color: rgb(14, 12, 3);
  padding: 5px;
  margin-top: 15px;
  display: flex;
  border-radius: 20px;
}
.dropdown-item {
  background-color:  rgb(186, 238, 207);
  color: rgb(14, 12, 3);
  font-family: Arial, Helvetica, sans-serif;
}


.container {
  display: flex;
  flex-wrap: wrap;
  padding: 20px;
  justify-content: center;
  gap: 30px;
}

.card {
  width: 18rem;
  display: flex;
  flex-direction: column;
  padding: 10px;
  border-radius: 10px;
  box-shadow: 0px 0px 33px 0px rgba(0, 0, 0, 0.2);
}

.card:hover {
  box-shadow: 0px 0px 33px 0px rgba(10, 3, 142, 0.5);
}

.img-container {
  width: 100%;
  height: 10rem;
  cursor: pointer;
}

 img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.text-title {
  font-size: large;
  font-weight: 900;
  text-align: center;
}
.products-section {
  text-align: center;
  margin-top: 20px;
  
}

.backdrop {
  width: 100vw;
  height: 100vh;
  background-color: rgba(53, 114, 254, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.popup {
  background-color: #fff;
  width: 50%;
  height: 50%;
  border-radius: 20px;
  display: flex;
  justify-content: space-evenly;
   
  
  padding: 0px;
}
.popup-image-container {
  flex:0.5;
  width: 100%;
  height: 100%;
}
.popup-body {
  flex: 0.5;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.modal-image-container {
   width: 50%;
   display: flex;
   align-items: center;
    
}
.modal-image-container img {
  width: 100%;

  margin: 10px;
  padding: 30px;
}
.footer footer{
  background-color: rgb(1, 71, 72);
  padding: 20px;
  position: sticky;
  bottom: 0;
}
.img-fluid {
  width:100px;
  border-radius:50px;
}

.productdetail {
  display: flex;
  flex-direction:column;
  align-items:center;
}
.descriptiondetail{
  width:25%;
}


.navigation {
  position: sticky;
  top: 0;
  left: 0;
}

.sign {
  background-color: rgb(252, 252, 200);
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.signtext {
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
}

.signima {
  width: 100%;
  height: auto;
   
   
}
.btn-signin{
  background-color: blue;
  width: 100%;
  height: 45px;
  border-radius: 10px;
  color: white;
  font-size: larger;
  margin-bottom: 25px;
  margin-top: 25px;
  }

  .line{
   width: 45%;
   height: 3px;
   background: rgb(60, 59, 59);
   margin-bottom: 3px; 
  }
  .linecontainer {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .text-center{
    font-size:1.6rem;
  }
.google-btn {
  width: 100%;
  background-color: rgb(227, 68, 68);
  margin-bottom: 25px;
  height: 45px;
  border-radius: 10px;
  color: #fff;
}

.google-icon {
  font-size: 1.5rem;
  margin: 5px;
}










@media only screen and (max-width:1000px) {
  .popup{
    width: 40%;
    height: 80%;
    flex-direction: column;
  }
  .popup-image-container{
    flex:0.5;
    width: 100%;
    height: 100%;
  }
  
}






@media only screen and (max-width:737px) {
   header{
 flex-direction: column;
   }
   header img {
    width: 50px;
    height: auto;
    border-radius: 25px;
     
  }
  header input {
    outline: none;
    padding: 10px;
    height: 50px;
    border-radius: 15px;
    flex: 0.5;
  }
  #login {
    font-size: small;
  }

  }
 
 